import React, { useState } from 'react'
import {  Button } from "reactstrap";
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import mTrans from '../../../Controler/c_Transaksi'
import Notifikasi from '../../../helpers/Notifikasi'
import { isArray } from "util";




const Componen = (props) => {
    const authLogin = props.authLogin
    const [penyakit, setPenyakit] = useState("")

    const closeFrom = () => {
        props.setOpen(false)
    }
    const simpanForm = () => {
        if(penyakit.length <= 0) return  Notifikasi("warning", "Wajib isi penyakit")
        mTrans.saveRiwayatPenyakit(authLogin.clientID, authLogin.token, props.pasien, penyakit).then(resp => {
            if (resp) {
                Notifikasi("success", "Berhasil disimpan", "")
                props.setIsReload(true)
                closeFrom()
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    return (
        <React.Fragment>
            <Dialog open={props.open} onClose={closeFrom} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Add Riwayat Penyakit</DialogTitle>
                <DialogContent>
                    <TextField
                        required
                        autoFocus
                        margin="dense"
                        label="Penyakit"
                        fullWidth
                        value={penyakit}
                        variant="outlined"
                        onChange={(e) => setPenyakit(e.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={closeFrom} color="secondary">Batal</Button>
                    <Button onClick={simpanForm} color="primary">Simpan</Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}

export default Componen;
