import React, { useState, useEffect } from 'react'
import { Row, Col } from "reactstrap";
import { Toolbar, Typography, IconButton, Button } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import NoImage from '../../../assets/image/NoImage'
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import SwipeableViews from 'react-swipeable-views';
import { Tabs, Tab } from '@material-ui/core';
import CompRekamMedis from './Comp.RekamMedis'
import CompPeralatan from './Comp.Peralatan'
import CompObat from './Comp.Obat'
import CompLayanan from './Comp.Layanan'
import moment from 'moment-timezone'
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Notifikasi from '../../../helpers/Notifikasi'
import ConfirmMUI from '../../../helpers/Confirm-MUI'
import FormPenyakit from './Comp.FormPenyakit'
import mTrans from '../../../Controler/c_Transaksi'
import { isArray } from "util";




const Modal = (props) => {
    const authLogin = props.authLogin
    const transOri = props.dataTrans || {}
    const [loadPenyakit, setLoadPenyakit] = useState(true)
    const [isConfirm, setIsConfirm] = useState(false)
    const [textConfirm, setTextConfirm] = useState("")
    const [value, setValue] = useState(0);
    const [isFormPenyakit, setIsFormPenyakit] = useState(false)
    const [listRiwayat, setListRiwayat] = useState([])
    const [pasien, setPasien] = useState({})
    const [listProduk, setListProduk] = useState([])
    const [listLayanan, setListLayanan] = useState([])
    const [form, setForm] = useState({
        catatanDokter: "",
        isDatangKembali: false,
        datangTgl: moment(),
        keterangan: ""
    })

    useEffect(() => {
        mTrans.dataPasien(authLogin.clientID, authLogin.token, transOri.pasien).then(resp => {
            setPasien(resp)
            if (resp.riwayatPenyakit.length > 0) setListRiwayat(resp.riwayatPenyakit);
        }).catch(err => {
            console.error(err);
        }).finally(() => setLoadPenyakit(false))
        return () => {

        }
    }, [loadPenyakit, authLogin.clientID, authLogin.token, transOri.pasien])

    useEffect(() => {
        mTrans.loadProduk(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik).then(resp => {
            setListProduk(resp.produk)
            setListLayanan(resp.layanan)
        }).catch(err => {
            console.error(err);
        })
        return () => {

        }
    }, [authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik])

    const handleClose = () => {
        props.setOpen(false)
    }
    function handleMenu(event, newValue) {
        setValue(newValue);
    }
    function handleMenuIndex(index) {
        setValue(index);
    }
    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        if (['isDatangKembali'].indexOf(key) > -1) {
            value = !!!form[key];
        }
        var newForm = { ...form }
        newForm[key] = value;
        setForm(newForm);
    }
    const confirmSave = () => {
        if (form.catatanDokter.length <= 0) return Notifikasi("warning", "Wajib isi catatan untuk pasien !!")
        setTextConfirm(`Apakah anda telah selesai memeriksa pasien? \n Tekan OK untuk lanjutkan simpan`)
        setIsConfirm(true)
    }
    const handleSave = () => {
        let tanggal = ""
        let ket = ""
        if (form.isDatangKembali) {
            tanggal = moment(form.tanggal).format("YYYY-MM-DD")
            ket = form.keterangan
        }
        const _catatanDokter = form.catatanDokter.replace(/\n/g, "<BR>");
        mTrans.finishPemeriksaanDokter(authLogin.clientID, authLogin.token, transOri._id, _catatanDokter, form.isDatangKembali, tanggal, ket).then(resp => {
            if (resp) {
                Notifikasi("success", "Berhasil disimpan, proses pemeriksaan pasien telah selesai", "")
                props.setIsReload(true)
                setIsConfirm(false)
                handleClose()

            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    return (
        <Dialog fullScreen open={props.open} onClose={handleClose} TransitionComponent={Transition}>
            <AppBar>
                <Toolbar>
                    <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                        <CloseIcon />
                    </IconButton>
                    <Typography variant="h6" style={{ flex: 1 }}>
                        Pemeriksaan Pasien
                    </Typography>
                    <Button autoFocus color="inherit" onClick={confirmSave}>Save</Button>
                </Toolbar>
            </AppBar>

            <div className="contentManual bodyDialog">
                <Row style={{ marginTop: 20, marginBottom: 20 }}>
                    <Col xs={12} md={3}>
                        <img className="imgDokter mx-auto d-block" alt="Dokter" src={`data:image/jpeg;base64,${NoImage}`} />
                        <span className="title-dokter d-block">{pasien.nama}</span><br />
                        <span className="jadwal-dokter d-block"><b>Keperluan :</b> <i> {transOri.keperluanPasien}</i></span>
                        <Button variant="outlined" color="secondary" size="small" onClick={() => setIsFormPenyakit(true)}> <AddCircleOutlineIcon />  Riwayat Penyakit</Button>
                        {listRiwayat.map((el, i) => {
                            return (
                                <p key={i}>{i + 1}. {el.penyakit}</p>
                            )
                        })}

                        <div style={{ marginTop: 5 }}>
                            <Row>
                                <Col>
                                    <TextField
                                        required
                                        margin="dense"
                                        id="catatanDokter"
                                        label="Catatan Untuk Pasien"
                                        fullWidth
                                        multiline={true}
                                        rows={3}
                                        value={form.catatanDokter}
                                        onChange={handleChange}
                                        variant="outlined"
                                    />
                                    <FormControlLabel control={<Checkbox checked={form.isDatangKembali || false} onChange={handleChange} />} id="isDatangKembali" name="isDatangKembali" label="Pasien wajib datang kembali" />
                                    {form.isDatangKembali && (
                                        <React.Fragment>
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <DatePicker
                                                    variant="outlined"
                                                    fullWidth={true}
                                                    label="Tanggal Datang"
                                                    format="dd-MM-yyyy"
                                                    value={form.datangTgl}
                                                    onChange={(d) => setForm({ ...form, datangTgl: d })}
                                                    ampm={false}
                                                />
                                            </MuiPickersUtilsProvider>
                                            <TextField
                                                margin="dense"
                                                id="keterangan"
                                                label="Keterangan"
                                                fullWidth
                                                value={form.keterangan}
                                                onChange={handleChange}
                                            />
                                        </React.Fragment>
                                    )}
                                </Col>
                            </Row>
                        </div>
                    </Col>
                    <Col xs={12} md={9}>
                        <Tabs
                            value={value}
                            onChange={handleMenu}
                            indicatorColor="primary"
                            textColor="primary"
                            centered
                        >
                            <Tab label="Rekam Medis" className="font12" />
                            <Tab label="Layanan" className="font12" />
                            <Tab label="Peralatan" className="font12" />
                            <Tab label="Resep Obat" className="font12" />
                            {/* <Tab label="Catatan" className="font12" /> */}
                        </Tabs>
                        <SwipeableViews
                            index={value}
                            onChangeIndex={handleMenuIndex}
                        >
                            <div value={value} index={0}>
                                <CompRekamMedis authLogin={authLogin} transOri={transOri} />
                            </div>
                            <div value={value} index={1}>
                                <CompLayanan authLogin={authLogin} transOri={transOri} listLayanan={listLayanan}/>
                            </div>
                            <div value={value} index={2}>
                                <CompPeralatan authLogin={authLogin} transOri={transOri} listProduk={listProduk} />
                            </div>
                            <div value={value} index={3}>
                                <CompObat authLogin={authLogin} transOri={transOri} listProduk={listProduk} />
                            </div>
                            {/* <div value={value} index={4}>
                                <div className="bgTabelModal pdFormModal">
                                  
                                </div>
                            </div> */}
                        </SwipeableViews>
                    </Col>
                </Row>
                {isConfirm && <ConfirmMUI open={isConfirm} setOpen={setIsConfirm} textConfirm={textConfirm} oke={handleSave} />}
                {isFormPenyakit && <FormPenyakit open={isFormPenyakit} setOpen={setIsFormPenyakit} setIsReload={setLoadPenyakit} pasien={transOri.pasien} authLogin={authLogin} />}
            </div>
        </Dialog>
    )
}

export default Modal;
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});