import React, { useState, useEffect } from 'react'
import ReactTable from "react-table"
import { CardBody, Button } from "reactstrap";
import DataTablePagination from "../../../components/DatatablePagination"
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import mTrans from '../../../Controler/c_Transaksi'
import Notifikasi from '../../../helpers/Notifikasi'
import { isArray } from "util";
import numberWithCommas from "reactnumberwithcommas";




const Componen = (props) => {
    const authLogin = props.authLogin
    const transOri = props.transOri
    const [isOpenForm, setIsOpenForm] = useState(false)
    const [produk, setProduk] = useState("")
    const [qty, setQty] = useState(1)
    const [listResepObat, setlistResepObat] = useState([])
    const [isLoad, setIsLoad] = useState(true)
    const [isEdit, setIsEdit] = useState(false)
    const [keterangan, setKeterangan] = useState("")

    useEffect(() => {
        mTrans.listResepObat(authLogin.clientID, authLogin.token, transOri._id).then(resp => {
            setlistResepObat(resp)
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsLoad(false))
        return () => {

        }
    }, [authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, transOri._id, isLoad])
    const dataTableColumns = [
        {
            Header: "Produk",
            accessor: "namaProduk",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "sataun",
            accessor: "satuan",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "harga",
            accessor: "harga",
            Cell: props => <p className="text-left"> {numberWithCommas(props.value)}</p>
        },
        {
            Header: "Qty",
            accessor: "qty",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Ket",
            accessor: "keterangan",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: <p className="text-center">Aksi</p>,
            Cell: props => <p className="text-muted">
                <Button style={{ marginRight: 3 }} color="warning" onClick={handleEdit.bind(this, props.original)}>Edit</Button>
                <Button color="danger" onClick={hapus.bind(this, props.original)}>Hapus</Button>
            </p>
        }
    ];
    const handleAdd = () => {
        setIsOpenForm(true)
        setIsEdit(false)
        setProduk("")
    }
    const handleEdit = data => {
        setIsEdit(true)
        setQty(data.qty)
        setProduk(data)
        setIsOpenForm(true)
        setKeterangan(data.keterangan)
    }
    const hapus = data => {
        mTrans.hapusResepObat(authLogin.clientID, authLogin.token,transOri._id, data.kodeProduk).then(resp => {
            if (resp) {
                Notifikasi("success", "Berhasil dihapus", "")
                setIsLoad(true)
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    const closeFrom = () => {
        setIsOpenForm(false)
    }
    const simpanForm = () => {
        if(!produk.kodeProduk) return  Notifikasi("warning", "Wajib isi pilih obat untuk pasien !!")
        if (isEdit) {
            mTrans.editResepObat(authLogin.clientID, authLogin.token,transOri._id, produk, qty, keterangan).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil diupdate", "")
                    setIsLoad(true)
                    closeFrom()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        } else {
            mTrans.simpanResepObat(authLogin.clientID, authLogin.token,transOri._id, produk, qty, keterangan).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil disimpan", "")
                    setIsLoad(true)
                    closeFrom()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }
    }
    return (
        <React.Fragment>
            <div className="bgTabelModal">
                <CardBody>
                    <Button
                        color="primary"
                        size="lg"
                        className="top-right-button"
                        onClick={handleAdd}
                    >Add Obat</Button>
                    <ReactTable
                        data={listResepObat}
                        columns={dataTableColumns}
                        defaultPageSize={5}
                        showPageJump={true}
                        PaginationComponent={DataTablePagination}
                        showPageSizeOptions={true}
                    />
                </CardBody>
            </div>
            <Dialog open={isOpenForm} onClose={closeFrom} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Add Obat</DialogTitle>
                <DialogContent>
                    <Autocomplete
                        style={{ marginTop: 15 }}
                        id="combo-box-demo"
                        options={props.listProduk}
                        getOptionLabel={(option) => option.namaProduk}
                        fullWidth
                        value={produk}
                        onChange={(event, values) => setProduk(values)}
                        renderInput={(params) => <TextField {...params} label="Pilih Resep Obat" variant="outlined" />}
                    />
                    <TextField
                        required
                        margin="dense"
                        id="Qty"
                        label="Qty"
                        fullWidth
                        value={qty}
                        variant="outlined"
                        type="number"
                        onChange={(e) => setQty(e.target.value)}
                    />
                    <TextField
                        required
                        margin="dense"
                        id="Keterangan"
                        label="Keterangan"
                        fullWidth
                        value={keterangan}
                        variant="outlined"
                        onChange={(e) => setKeterangan(e.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={closeFrom} color="secondary">Batal</Button>
                    <Button onClick={simpanForm} color="primary">Simpan</Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}

export default Componen;

