import React, { useState, useEffect } from 'react'
import ReactTable from "react-table"
import { CardBody, Button } from "reactstrap";
import DataTablePagination from "../../../components/DatatablePagination"
import mTrans from '../../../Controler/c_Transaksi'
import moment from 'moment-timezone'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';




const Componen = (props) => {
    const authLogin = props.authLogin
    const transOri = props.transOri || {}
    const [listRekamMedis, setListRekamMedis] = useState([])
    const [isOpenForm, setIsOpenForm] = useState(false)
    const [dataSelect, setDataSelect] = useState({})
    useEffect(() => {
        mTrans.rekamMedis(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, transOri.pasien).then(resp => {
            console.log(resp)
            setListRekamMedis(resp)
        }).catch(err => {
            console.error(err);
        })
        return () => {

        }
    }, [authLogin.clientID, authLogin.token, transOri.pasien, authLogin.klinik.kodeklinik])
    const dataTableColumns = [
        {
            Header: "Tanggal",
            accessor: "tanggalJanji",
            Cell: props => <p className="list-item-heading"> {moment(props.value).format("DD-MM-YYYY")}</p>
        },
        {
            Header: "Keperluan",
            accessor: "keperluanPasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Catatan Dokter",
            accessor: "catatanDokter",
            width: 100,
            Cell: props => <p className="text-left"> {props.value}</p>,

        },
        {
            Header: <p className="text-center">Detail</p>,
            filterable: false,
            Cell: props => <p className="text-center">
                <Button color="warning" onClick={viewDetail.bind(this, props.original)}>Lihat</Button>
            </p>
        }
    ];
    const viewDetail = data => {
        setIsOpenForm(true)
        setDataSelect(data)
    }
    const closeFrom = () => {
        setIsOpenForm(false)
    }
    return (
        <React.Fragment>
            <div className="bgTabelModal">
                <CardBody>
                    {listRekamMedis.length <= 0 ? (
                        <p className="text-center"><b>Pasien baru pertama kali berobat kesini</b></p>
                    ) : (
                            <ReactTable
                                data={listRekamMedis}
                                columns={dataTableColumns}
                                defaultPageSize={5}
                                showPageJump={true}
                                PaginationComponent={DataTablePagination}
                                showPageSizeOptions={true}
                            />
                        )}

                </CardBody>
            </div>
            {isOpenForm && (
                <Dialog open={isOpenForm} onClose={closeFrom} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Rekam Medis</DialogTitle>
                    <DialogContent>
                        <h6>Tanggal : <b>{moment(dataSelect.janji.tanggalJanji).format("DD-MM-YYYY")}</b></h6>
                        <h6>Keperluan : <b>{dataSelect.keperluanPasien}</b></h6>
                        <h6>Catatan Dokter : <b>{dataSelect.catatanDokter}</b></h6>
                        <h6><b>Layanan</b></h6>
                        {dataSelect.layanan.length > 0 && dataSelect.layanan.map((el, i) => (
                            <p index={i}>- {el.namaLayanan} * {el.qty} {el.keterangan && (<small> ({el.keterangan}) </small>)}</p>
                        ))}
                        <h6><b>Obat Dari Dokter</b></h6>
                        {dataSelect.resepObatdokter.length > 0 && dataSelect.resepObatdokter.map((el, i) => (
                            <p index={i}>- {el.namaProduk} * {el.qty} {el.keterangan && (<small> ({el.keterangan}) </small>)}</p>
                        ))}
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={closeFrom} color="primary">Close</Button>
                    </DialogActions>
                </Dialog>
            )}
        </React.Fragment>
    )
}

export default Componen;
