import React, { Fragment, useState, useEffect } from "react";
import { Row, Col, CardBody, Button } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { Tabs, Tab } from '@material-ui/core';
import mTrans from '../../../Controler/c_Transaksi'
import moment from 'moment-timezone'
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DataTablePagination from "../../../components/DatatablePagination"
import Skeleton from 'react-loading-skeleton';
import ReactTable from "react-table"
import { ContextMenuTrigger } from "react-contextmenu"
import SwipeableViews from 'react-swipeable-views';
import ModalPemeriksaan from './Modal.Pemeriksaan'

const BuatJanji = props => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [isReload, setIsReload] = useState(true)
    const [listJanji, setListJanji] = useState([])
    const [listDiperiksa, setlistDiperiksa] = useState([])
    const [tanggal, setTanggal] = useState(moment())
    const [value, setValue] = useState(0);
    const [dataTrans, setDataTrans] = useState({})
    const [isPemeriksaan, setIsPemeriksaan] = useState(false)
    const [listBuatJanji, setListBuatJanji] = useState([])


    useEffect(() => {
        setListJanji([])
        mTrans.listJanjiByDokter(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, moment(tanggal).format("YYYY-MM-DD"), authLogin.username).then(resp => {
            setListJanji(resp.listJanji);
            setlistDiperiksa(resp.listDiperiksa)
            setListBuatJanji(resp.buatJanji)
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => { };
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.klinik.kodeklinik, tanggal, isReload]);

    const loadingRow = [0, 1, 2, 3, 4, 5];
    const dataTableColumns = [
        {
            Header: "Pasien",
            accessor: "pasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Keperluan",
            accessor: "keperluanPasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Jam Mulai",
            accessor: "jamMulaiJanji",
            width: 100,
            Cell: props => <p className="text-left"> {props.value}</p>,

        },
        {
            Header: "Jam Selesai",
            accessor: "jamSelesaiJanji",
            width: 100,
            Cell: props => <p className="text-left"> {props.value}</p>,
        }
    ];

    const dataTableDiperiksa = [
        {
            Header: "Pasien",
            accessor: "pasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Keperluan",
            accessor: "keperluanPasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Jam Mulai",
            accessor: "jamMulaiJanji",
            width: 100,
            Cell: props => <p className="text-left"> {props.value}</p>,

        },
        {
            Header: "Jam Selesai",
            accessor: "jamSelesaiJanji",
            width: 100,
            Cell: props => <p className="text-left"> {props.value}</p>,
        },
        {
            Header: <p className="text-center">Aksi</p>,
            filterable: false,
            Cell: props => <p className="text-center">
                <Button style={{ marginRight: 3 }} color="warning" onClick={handlePeriksa.bind(this, props.original)}>Pemeriksaan</Button>
            </p>
        }
    ];
    const handlePeriksa = data => {
        setDataTrans(data)
        setIsPemeriksaan(true)
    }
    function handleChange(event, newValue) {
        setValue(newValue);
    }
    function handleChangeIndex(index) {
        setValue(index);
    }
    return (
        <Fragment>
            <Row>
                <Colxx xxs="12">
                    <Breadcrumb heading="menu.listjanji" match={props.match} />
                    <Separator className="mb-5" />
                </Colxx>
            </Row>
            <Row>
                <Col xs={12}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DatePicker
                            fullWidth={true}
                            label="Pilih Tanggal"
                            format="dd-MM-yyyy"
                            value={tanggal}
                            onChange={(d) => setTanggal(d)}
                            ampm={false}
                        />
                    </MuiPickersUtilsProvider>
                </Col>
            </Row>
            <Tabs
                value={value}
                onChange={handleChange}
                indicatorColor="primary"
                textColor="primary"
                centered
            >
                <Tab label="Janji" className="font12" />
                <Tab label="Antrian" className="font12" />
                <Tab label="Yang Diperiksa" className="font12" />
            </Tabs>
            <SwipeableViews
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <div value={value} index={0}>
                    {isReload && (
                        <Col xs={12}>
                            <Card className="tangan mgTop10">
                                <CardContent>
                                    {loadingRow.map((el, i) => (
                                        <Skeleton height={35} key={i} />
                                    ))}
                                </CardContent>
                            </Card>
                        </Col>
                    )}
                    {(!isReload && listJanji.length === 0) && (
                        <Col xs={12} style={{ marginTop: 30 }}>
                            <h4 className="text-center2"><b>Belum ada yang buat janji untuk tanggal <i>{moment(tanggal).format("DD-MM-YYYY")}</i></b></h4>
                        </Col>
                    )}
                    {listJanji.length > 0 && (
                        <Col xs={12}>
                            <ContextMenuTrigger id="menu_id" >
                                <Card>
                                    <CardBody>
                                        <ReactTable
                                            data={listBuatJanji}
                                            columns={dataTableColumns}
                                            defaultPageSize={5}
                                            showPageJump={true}
                                            PaginationComponent={DataTablePagination}
                                            showPageSizeOptions={true}
                                            filterable
                                            defaultFilterMethod={(filter, row) => {
                                                const id = filter.pivotId || filter.id;
                                                return row[id] !== undefined
                                                    ? String(row[id].toLowerCase()).includes(
                                                        filter.value.toLowerCase()
                                                    )
                                                    : true;
                                            }}
                                        />
                                    </CardBody>
                                </Card>
                            </ContextMenuTrigger>
                        </Col>
                    )}
                </div>
                <div value={value} index={1}>
                    {isReload && (
                        <Col xs={12}>
                            <Card className="tangan mgTop10">
                                <CardContent>
                                    {loadingRow.map((el, i) => (
                                        <Skeleton height={35} key={i} />
                                    ))}
                                </CardContent>
                            </Card>
                        </Col>
                    )}
                    {(!isReload && listJanji.length === 0) && (
                        <Col xs={12} style={{ marginTop: 30 }}>
                            <h4 className="text-center2"><b>Belum ada yang antri untuk tanggal <i>{moment(tanggal).format("DD-MM-YYYY")}</i></b></h4>
                        </Col>
                    )}
                    {listJanji.length > 0 && (
                        <Col xs={12}>
                            <ContextMenuTrigger id="menu_id" >
                                <Card>
                                    <CardBody>
                                        <ReactTable
                                            data={listJanji}
                                            columns={dataTableColumns}
                                            defaultPageSize={5}
                                            showPageJump={true}
                                            PaginationComponent={DataTablePagination}
                                            showPageSizeOptions={true}
                                            filterable
                                            defaultFilterMethod={(filter, row) => {
                                                const id = filter.pivotId || filter.id;
                                                return row[id] !== undefined
                                                    ? String(row[id].toLowerCase()).includes(
                                                        filter.value.toLowerCase()
                                                    )
                                                    : true;
                                            }}
                                        />
                                    </CardBody>
                                </Card>
                            </ContextMenuTrigger>
                        </Col>
                    )}
                </div>
                <div value={value} index={2}>
                    {(!isReload && listDiperiksa.length === 0) && (
                        <Col xs={12} style={{ marginTop: 30 }}>
                            <h4 className="text-center2"><b>Belum ada pasien yang sedang diperiksa</b></h4>
                        </Col>
                    )}
                    {listDiperiksa.length > 0 && (
                        <Col xs={12}>
                            <ContextMenuTrigger id="menu_id2" >
                                <Card>
                                    <CardBody>
                                        <ReactTable
                                            data={listDiperiksa}
                                            columns={dataTableDiperiksa}
                                            defaultPageSize={5}
                                            showPageJump={true}
                                            PaginationComponent={DataTablePagination}
                                            showPageSizeOptions={true}
                                        />
                                    </CardBody>
                                </Card>
                            </ContextMenuTrigger>
                        </Col>
                    )}
                </div>

            </SwipeableViews>
            {isPemeriksaan && <ModalPemeriksaan open={isPemeriksaan} setOpen={setIsPemeriksaan} dataTrans={dataTrans} authLogin={authLogin} setIsReload={setIsReload} />}
        </Fragment>
    )
}
export default BuatJanji
